/* ===================Register================ */
.main {
  background: #f8f8f8;
  padding: 10rem 0;
}
.container {
  width: 900px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0px 15px 16.83px 0.17px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
}
.signup-content,
.signin-content {
  display: flex;
  padding: 75px 0;
}
.signup-form,
.signup-image,
.signin-form,
.signin-image {
  width: 50%;
  overflow: hidden;
  margin: 0 75px;
  padding-left: 34px;
}
h2 {
  line-height: 1.66;
  margin: 0;
  padding: 0;
  font-weight: 700;
  color: #222;
  font-family: Poppins;
  font-size: 36px;
}
.register-form {
  width: 100%;
}
.form-group {
  position: relative;
  margin-bottom: 25px;
  overflow: hidden;
}
.label {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #222;
}
input {
  width: 100%;
  display: block;
  border: none;
  border-bottom: 1px solid #999;
  padding: 6px 30px;
  font-family: Poppins;
  box-sizing: border-box;
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
}
.signup-image {
  margin: 45px 55px 0 55px;
}
figure {
  margin-bottom: 50px;
  text-align: center;
}
img {
  max-width: 100%;
  /* height: auto; */
}
.form-submit {
  display: inline-block;
  background: #6dabe4;
  color: #fff;
  border-bottom: none;
  width: auto;
  padding: 15px 39px;
  border-radius: 5px;
  margin-top: 25px;
  cursor: pointer;
}
.form-submit:hover {
  background: #4292dc;
}

.signup-image-link {
  font-size: 14px;
  color: #222;
  display: block;
  text-align: center;
}
.form-group:last-child {
  margin-bottom: 0px;
}

.signin-form {
  margin-left: 90px;
  margin-left: 80px;
}

.signin-image {
  margin-left: 110px;
  margin-right: 20px;
  margin-top: 10px;
}

.chkbox {
  width: 20px;
  height: 3rem;
}
.remember-me {
  margin-left: 2rem;
  margin-top: 0.5rem;
  /* height: 4rem;/ */
  font-size: 14px;
}

.forgot-btn:hover {
  color: black !important;
  background-color: white;
  /* outline: none; */
  border: 1px solid white;
}

.usermenu {
  margin-left: 5rem;
}

@media screen and (max-width: 1024px) {
  /* .container {
    max-width: 1200px;
  } */

  .signup-image,
  .signin-image {
    margin: 0 20px;
  }
  .signup-form,
  .signin-form {
    margin: 0 10px;
  }
}
@media screen and (max-width: 768px) {
  .signup-content,
  .signin-content {
    flex-direction: column;
    justify-content: center;
  }

  .signup-form {
    margin-left: 0px;
    margin-right: 0px;
    padding: 0 30px;
  }

  .signin-image {
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 50px;
    order: 2;
  }

  .signup-form,
  .signup-image,
  .signin-form,
  .signin-image {
    width: auto;
  }

  /* .social-login {
      justify-content: center;
    } */

  .form-button {
    text-align: center;
  }
  .btn-primary {
    text-align: center;
    display: flex;
    margin: auto;
  }

  .signin-form {
    order: 1;
    margin-right: 0px;
    margin-left: 0px;
    padding: 0 30px;
  }

  .form-title {
    text-align: center;
  }
}
.btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user-profile {
  width: 100%;
  /* background: #fff; */
  margin: 0 auto;
  box-shadow: none;
  border-radius: none;
}
.user-content {
  padding: 34px 0;
}
.user-content h2 {
  margin: 0;
  font-size: 30px;
}
@media screen and (max-width: 480px) {
  .main {
    padding: 2rem 0;
    background: #fff;
  }
  .container {
    width: 100%;
  }
}
/* ===================================================== */
