.shop h1 {
  margin-top: 3rem;
  color: #000;
  font-family: "Playfair Display", serif;
}

.shop {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  /* background-color: red; */
  /* align-items: center; */
}

.card-body {
  padding: 0.7rem;
}

.shop .card {
  /* background-color: rgba(128, 128, 128, 0.097); */
  width: 18rem;
  border: none;
}
.shop .card-body {
  border: 1px solid rgba(128, 128, 128, 0.097);
  flex: 1 1 auto;
}

.shop .card-name-price-h {
  display: flex;
  /* flex-direction: column; */
  flex-direction: column;
  /* justify-content: space-between; */
  /* font-size: 16px; */
}
.shop .card-name-price {
  display: flex;
  /* flex-direction: column; */
  flex-direction: row;
  justify-content: space-between;
  /* font-size: 16px; */
}

.shop .card-price {
  color: #32409a;
  font-weight: bold;
  /* font-size: 1.2rem; */
}
.shop button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  /* flex-direction: row; */
}

.btn-info {
  background: #212b67;
  color: #fff;
}
.btn-info:hover {
  color: #fff;
  background: #32409a;
}
.shop .card-text {
  color: rgb(90, 89, 89);
  text-align: justify;
  /* color: #000; */
}

.shop .card img:hover {
  transform: scale(1.1);
  /* box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5); */
  /* transition: transform 1s, filter 2s ease-in-out; */
}

.shop .card-img-top {
  height: 300px !important;
}
/* ==================
      filters
      ============== */
.filters {
  padding: 1rem 1.5rem;
  border: 1px solid #cccccc;
  height: 100%;
  margin-top: 3rem;
  /* background-color: green; */
  width: 20%;
  margin-left: 4rem;
}

.filters h4 {
  /* margin-top: 100px; */
  color: #000 !important;
  /* border-bottom: 1px solid rgba(128, 128, 128, 0.337); */
  margin-bottom: 1.5rem;
}

.ant-checkbox-wrapper,
.ant-radio-wrapper {
  position: relative;
  padding: 0.3rem 1rem;
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 0.1px;
}

.filters button {
  background-color: #212b67;
  width: 100%;
  border: none;
  border-radius: 0;
  /* margin-top: 20px; */
}
.filters button:hover {
  background-color: #32409a;
}
.srch {
  border-radius: 50px;
  border-color: #999999;
  height: 3rem;
}
.srch-btn {
  width: 50px !important;
  height: 50px;
  border-radius: 50px !important;
  border: none !important;
  position: absolute;
  color: #fff;
  left: 22%;
}

.loadmore {
  color: #32409a;
  font-weight: bold;
  font-size: 20px !important;
  border-style: none;
}
.loadmore:hover {
  color: #212b67;
}

.col-md-9 {
  margin: 3rem;
  /* margin: 3rem 0; */
  /* background-color: yellow; */
  width: 61%;
}

@media only screen and (max-width: 1024px) {
  .filters {
    width: 28%;
    margin-left: 1rem;
  }

  .srch-btn {
    left: 24%;
  }

  .col-md-9 {
    width: 65%;
    margin: 3rem 1rem;
    /* background: red; */
  }
  .p-card {
    justify-content: space-evenly;
  }

  .shop .card {
    width: 11rem;
  }
  .shop button {
    font-size: 12px;
  }

  .shop .card-img-top {
    height: 200px !important;
  }
}

@media only screen and (max-width: 768px) {
  .shop h1 {
    margin: 0;
    font-size: 2rem;
  }

  .card-body {
    padding: 0.5rem;
  }

  .shop .card {
    width: 12rem;
  }
  .shop button {
    font-size: 8px;
  }

  .shop .card-img-top {
    height: 220px !important;
  }
  .filters {
    padding: 1rem;
  }

  .filters h4 {
    margin: 0.5rem 0;
    font-size: 16px;
    font-weight: 700;
  }

  .ant-checkbox-wrapper,
  .ant-radio-wrapper {
    padding: 0.3rem;
  }

  .srch {
    margin: 0.5rem 0;
    font-size: 12px;
    height: 2.4rem;
  }
  .srch-btn {
    width: 41px !important;
    height: 41px;
    left: 24%;
    margin: 0.4rem 0;
  }

  .loadmore {
    font-size: 16px !important;
  }
}

@media only screen and (max-width: 480px) {
  .shop {
    justify-content: space-around;
  }
  .shop .card {
    width: 10.5rem;
  }
  .filters {
    width: 93%;
  }
  .srch-btn {
    left: 84%;
  }
  .col-md-9 {
    margin: 1rem 0rem;
    width: 93%;
  }
  .p-card {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 320px) {
  .shop .card {
    width: 16rem;
  }
  .srch-btn {
    left: 79%;
  }
}
