.row {
  width: 100%;
  /* background: yellow; */
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  margin: 0;
}
.blog-details {
  padding: 0 7rem;
  /* background: black; */
  height: 100%;
  display: flex;
}
.L-blog {
  width: 60rem;
  /* background: purple; */
  padding: 1rem 2rem 0 2rem;
}
.blog-lists {
  /* background: orange; */
  height: 100%;
}
.blog-card {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
  border-radius: 0;
  margin: 0.5rem 0;
  border: none;
}
.card-title {
  cursor: default;
}
.card .card-title h5 {
  font-size: 25.6px;
  text-justify: inter-word;
  text-align: center;
  font-weight: 700;
  cursor: default;
  /* width: 80%; */
}
.card .card-title h5 .product-link {
  cursor: pointer;
  /* padding-bottom: 15px; */
}
.card .card-title h5 p {
  font-size: 16px;
  font-weight: 400;
}
.margin {
  height: 3px;
  display: block;
  background-color: rgba(0, 0, 0, 0.1);
  margin: 0.8rem;
  width: 100%;
  max-width: 30px;
  margin-left: auto;
  margin-right: auto;
}
.card-description {
  /* padding-top: 1.5em; */
  padding-bottom: 1.5em;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  text-justify: distribute;
}

.R-blog {
  width: 25rem;
  /* background: hotpink; */
  padding: 1rem 2rem 0 2rem;
  border-left: 1.5px solid rgba(0, 0, 0, 0.1);
}
aside {
  /* background-color: yellow; */
  height: 100%;
  padding: 1rem 0;
  /* margin: 0 1rem; */
}
span.widget-title {
  font-size: 1.2em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  line-height: 1.05em;
  /* font-size: 25.6px; */
  /* text-justify: inter-word; */
  /* text-align: center; */
  /* font-weight: 700; */
  color: #000;
  cursor: default;
}
.R-blog .margin {
  margin-left: 0;
  margin-top: 0.66em;
}
aside ul {
  padding: 0;
  margin-bottom: 0;
  /* border: 2px solid black; */
}
aside ul li {
  list-style: none;
  font-weight: 600;
  font-size: 20px;
  /* background: green; */

  text-align: left !important;
}
.recent-posts-blogs {
  padding: 10px 0 10px;
  margin-bottom: 1.6px;
  line-height: 1.2;
  display: block;
  /* border-bottom: 1.5px solid rgba(0,0,0,.1); */
}
.recent-posts-blogs h5 {
  border-bottom: 1.5px solid rgba(0, 0, 0, 0.1);
  font-size: 18px;
  text-align: justify;
  line-height: 1.5;
  font-weight: 500;
  padding: 0.3rem 0;
}
@media only screen and (max-width: 1024px) {
  .card-img-top {
    height: 25% !important;
  }
  .blog-details {
    padding: 0 2rem;
  }
  .L-blog {
    padding-left: 0;
  }
  .R-blog {
    width: 25rem;
    /* background: hotpink; */
    padding-right: 0;
  }
}
@media only screen and (max-width: 768px) {
  .blog-details {
    padding: 0 2rem;
  }
  .L-blog {
    padding-left: 0;
  }
  .R-blog {
    width: 25rem;
    /* background: hotpink; */
    padding-right: 0;
  }
}
@media only screen and (max-width: 320px) {
  .blog-details {
    padding: 0 2rem;
  }
  .L-blog {
    padding: 0;
  }
  .R-blog {
    display: none;
  }
}
