@import url("https://fonts.googleapis.com/css2?family=Philosopher&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.navbar {
  color: white;
  padding: 0;
}
.nav1 {
  width: 100%;
  height: 60px;
  background: #268b3f;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 600;
}
.sb-nav1,
.sb-nav2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 7rem;
}
.nav1 .text {
  line-height: 3.7rem;
  height: 3.7rem;
}
.nav1 ul {
  display: flex;
}
.nav1 ul li {
  list-style: none;
  display: flex;
  margin-left: 1.3rem;
}
.sb-nav1 .items {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
}
.nav1 .social {
  display: flex;
  align-items: center;
  margin-right: 15px;
}
.nav2 {
  width: 100%;
  height: 130px;
}
.nav2 .text {
  line-height: 8.2rem;
  height: 8.2rem;
}
.nav2 .logo {
  cursor: pointer;
}
.nav2 .logo img {
  width: 116px;
  height: 87px;
}
.nav2 .search-box {
  position: relative;
}
.search-box input {
  width: 90%;
  height: 50px;
  border: 1px solid #e9e9e9;
  outline: none;
  background: #fff;
  color: #000;
  border-radius: 50px;
  padding-left: 2rem;
  margin-right: 13rem;
  display: block;
  margin-top: 2.5rem;
}
.search-box .icon {
  width: 129px;
  height: 65px;
  border-radius: 50px;
  border: none;
  position: absolute;
  top: 33px;
  right: 0;
  background: #32409a;
  color: white;
}
.search-box .srch {
  display: none;
}
.nav2 button {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: none;
  transform: scale(0.9);
  background: #32409a;
  color: white;
  font-size: 1.2rem;
}
.nav2 button:hover {
  color: #212b67;
}
.nav3 {
  width: 100%;
  height: 80px;
  background: #32409a;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
}
#chk1 {
  display: none;
}
.nav3 .menu {
  font-size: 2rem;
  position: relative;
  display: none;
}
.nav3 ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 7rem;
  height: 5rem;
  padding: 0;
}
.nav3 ul li {
  list-style: none;
}
.nav3 .nav-items {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
}
.nav3 .nav-items:hover {
  border-bottom: 2px solid #b1bcff;
}
.nav3 .active {
  border-bottom: 2px solid #b1bcff;
}
/*==========footer============= */
.footer {
  background: #32409a;
  color: white;
}
.foot1 {
  width: 100%;
  height: 80px;
  font-size: 14px;
}
.foot1 ul {
  display: flex;
  justify-content: space-between;
  font-family: "Poppins", sans-serif;
  margin: 0 7rem;
  padding: 0;
  border-bottom: 1px solid #c1caff;
}
.foot1 ul li {
  list-style: none;
  padding: 1.5rem 0;
}
.foot1 .nav-items {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
}
.foot1 .nav-items:hover {
  border-bottom: 2px solid #b1bcff;
}
.foot2 {
  width: 100%;
  margin: 2rem 0;
}
.sub-footer {
  display: flex;
  margin: 0rem 6rem;
  flex-wrap: wrap;
}
.cols {
  flex: 25%;
  padding: 20px;
}
.cols h4 {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 0.9rem;
  font-weight: bold;
}
.cols ul {
  display: flex;
}
.cols ul li {
  list-style: none;
  display: flex;
  flex-direction: column;
}
.cols .items {
  font-size: 16px;
  line-height: 35px;
  text-decoration: none;
  color: #fff;
}
.cols .details:hover {
  color: red;
}
.newsletter {
  margin: 0 1rem;
  display: flex;
}
.newsletter input {
  width: 100%;
  height: 50px;
  border: 1px solid #e9e9e9;
  outline: none;
  background: #fff;
  border-radius: 50px;
  padding-left: 2rem;
}
.newsletter button {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: none;
  position: absolute;
  right: 8.3rem;
  transform: scale(0.9);
  background: #212b67;
  color: white;
}
.mt-3 {
  margin-top: 0.7rem !important;
}
.foot3 {
  font-family: "Poppins", sans-serif;
  width: 100%;
  background: #212b67;
}
.sb-foot3 {
  padding: 1.8rem 0;
  margin: 0 7rem;
  height: 84px;
}
.sb-foot3 h3 {
  font-size: 20px;
}
/* =================================*/

/*======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: #32409a;
  color: white;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
.dashboard {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.admin-product {
  text-align: justify;
}
.filter {
  padding: 1rem 0rem;
  /* border: 1px solid #cccccc; */
  height: 100%;
  margin-top: 2rem;
  /* background-color: green; */
  width: 20%;
  margin-left: 4rem;
}
.second {
  margin-left: 1rem;
  /* width: fit-content; */
}
/* ========================================= */

@media only screen and (max-width: 1024px) {
  .nav1 .sb-nav1,
  .nav2 .sb-nav2 {
    margin: 0 2rem;
  }
  .nav1 ul li {
    margin-left: 0.5rem;
  }
  .nav2 .logo img {
    width: 100px;
    height: 75px;
  }
  .search-box input {
    width: 90%;
    flex: 1;
    position: relative;
    top: 2.8rem;
    margin-top: 0;
    height: 45px;
  }
  .search-box .icon {
    width: 95px;
    height: 50px;
    top: 2.7rem;
  }
  .nav3 ul {
    margin: 0 2rem;
  }

  /* .filter-type-btn {
    margin-left: 3rem;
  } */
  .foot1 ul {
    margin: 0 2rem;
    padding-left: 0;
  }
  .sub-footer {
    margin: 0 2rem;
  }
  .cols {
    flex: 50%;
  }
  .cols h4 {
    font-size: 16px;
    line-height: 20px;
    font-weight: bold;
  }
  .cols .items,
  .cols p {
    font-size: 14px;
    line-height: 30px;
  }
  .sb-footer-links-div img,
  .cols img {
    width: 5rem;
    height: 5rem;
  }
  .newsletter button {
    right: 4.2rem;
  }
  .sb-foot3 {
    margin: 0 2rem;
  }
  .sb-foot3 h3 {
    font-size: 18px;
  }
  .filter {
    width: 25%;
    margin-left: 0rem;
  }
  .admin-blog {
    width: 43rem !important;
  }
}
@media only screen and (max-width: 768px) {
  .sb-nav1 {
    display: flex;
    flex-direction: column;
    margin: 0 1rem !important;
  }
  .nav1 .text {
    line-height: 1.8rem;
    height: 1.8rem;
    margin: 0 5px;
    font-size: 14px;
  }
  .nav1 ul {
    padding: 0;
  }
  .nav2 {
    height: 100px;
  }
  .sb-nav2 {
    height: 100px;
  }
  .nav2 .logo img {
    width: 90px;
    height: 70px;
  }
  .nav2 .search-box {
    flex: 1;
  }
  .search-box input {
    display: none;
  }
  .search-box .icon {
    display: none;
  }
  .search-box .srch {
    display: block;
    width: 50px;
    height: 50px;
    top: 2.7rem;
    right: 0;
    position: absolute;
    margin: 0;
  }
  .nav3 {
    height: 60px;
  }
  .nav3 .menu {
    display: block;
    width: 100px;
    text-align: center;
    margin: 0.5rem 2rem;
  }
  .nav3 ul {
    position: absolute;
    top: 0;
    left: -100%;
    background: #32409a;
    height: calc(100vh - 10rem);
    width: 50%;
    margin-top: 13.7rem;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transition: left 0.5s ease-in-out;
    margin-left: -2rem;
    z-index: 2;
    /* height: 3.7rem; */
  }
  #chk1:checked ~ ul {
    left: 0;
  }
  .w-75 h3 {
    font-size: 20px;
  }
  .dashboard-menu {
    font-size: 12px !important;
  }
  .crt-ctgy h1 {
    font-size: 26px;
    margin: 0;
  }
  .crt-category {
    display: flex;
    justify-content: center;
    margin-top: 0.1rem;
  }
  .category-name {
    font-size: 14px !important;
  }
  .p-1 {
    font-size: 12px;
  }
  .product-btn button,
  .blog-btn button {
    font-size: 12px;
  }
  .admin-product {
    width: 12rem !important;
  }
  .admin-blog {
    width: 30rem !important;
  }
  .first {
    width: 30%;
    height: 100%;
  }
  .nav3 {
    height: 60px;
  }

  .foot1 {
    display: none;
  }
  .foot2 .cols {
    border-bottom: 1px solid #c1caff;
  }

  .sub-footer {
    margin: 0 1rem;
    flex-direction: column;
  }
  .newsletter {
    width: 40%;
    margin: 0;
  }
  .newsletter button {
    left: 34.5%;
    position: absolute;
    margin-top: 0;
  }
  .newsletter p {
    margin-top: 0.5rem !important;
  }
  .sb-foot3 {
    margin: 0 1rem;
    height: 3.5rem;
  }
  .sb-foot3 h3 {
    font-size: 16px;
    margin-top: -0.5rem;
  }
}
@media only screen and (max-width: 480px) {
  .nav3 .menu {
    margin: 0.5rem 0rem;
  }
  .filter {
    width: 100%;
  }
  .user-dashboard-title {
    width: 37%;
  }
  .user-dashboard-menu {
    width: 100% !important;
    padding: 0;
  }
  .user-dashboard-submenu {
    width: 100% !important;
  }
  .admin-dashboard-submenu {
    margin-left: -25px;
  }
  .first {
    width: 200%;
    height: 100%;
  }
  .crt-ctgy {
    width: 100% !important;
  }
  .admin-blog {
    width: 28rem !important;
  }
}

@media only screen and (max-width: 374px) {
  .nav1 .text {
    font-size: 10px;
  }
  .nav1 ul {
    font-size: 10px;
    padding: 0;
  }
  .nav2 {
    height: 80px;
  }
  .sb-nav2 {
    height: 80px;
    margin: 0 1rem !important;
  }
  .sb-nav2 .text {
    height: 80px;
    display: flex;
    align-items: center;
  }
  .nav2 .logo img {
    width: 60px;
    height: 50px;
    position: absolute;
    top: 4.7rem;
  }
  .search-box .srch {
    width: 40px;
    height: 40px;
    top: 1.3rem;
    position: absolute;
  }
  .nav2 button {
    width: 40px;
    height: 40px;
  }

  .nav3 ul {
    margin-top: 12.5rem;
  }
  .sb-foot3 h3 {
    font-size: 14px;
  }
}
/* ======================================= */
