.aboutimgsection {
  margin: 3rem 7rem;
  /* margin-bottom: 5rem !important; */
  /* background: green; */
  /* border-bottom: 2px solid red; */
  display: flex;
  justify-content: space-between;
}
.asection {
  text-align: center;
  /* background: red; */
}
.asection p {
  /* line-height: 48px; */
  font-size: 32px;
  margin-bottom: 0;
  font-weight: 500;
  padding: 10px 0;
}
.aboutvideosection {
  margin: 3rem 7rem;
}
.a-comment {
  background: none;
  color: #000;
  width: 30%;

  text-align: center;
  justify-content: space-evenly;
  padding-top: 2rem;
  font-size: 36px;

  /* font-weight: 500; */
  margin: 3rem 34.7rem;
}
.a-comment p {
  margin: 0;
}

.comment-content {
  margin: 4rem 3rem 0rem 3rem;
}
.comment-content .card-title {
  display: flex;
  flex-direction: row;
  margin-bottom: 0;
}
.ccb {
  /* background:linear-gradient(135deg, #078ce3 0%,#32409a 100%); */
  background: #e7e7e7;
  background-image: url("../assets/Vector2.png");
  cursor: pointer;
  background-position: 70px 145px;
  background-repeat: no-repeat;
  color: #000;
}

.ccb .card-body {
  background-image: url("../assets/Vector2.png");
  background-repeat: no-repeat;
  background-position: 70px 80px;
  height: 350px;
}

.comment-content .card-text {
  margin-top: 2rem;
  /* text-align: center; */
  text-align: justify;
}
@media only screen and (max-width: 1200px) {
  .a-comment {
    margin: 3rem 27.7rem;
  }
  .a-comment p {
    font-size: 30px;
  }
}
@media only screen and (max-width: 1024px) {
  .a-comment {
    margin: 3rem 16.7rem;
    width: 50%;
  }
  .aboutimgsection,
  .aboutvideosection {
    margin: 3rem 2rem;
  }

  .asection p {
    font-size: 22px;
  }

  .ccb {
    background-position: 50px 80px !important;
  }

  .ccb .card-body {
    height: 280px;
  }
}
@media only screen and (max-width: 768px) {
  .a-comment {
    margin: 3rem 9.7rem;
    width: 60%;
    padding: 0;
    height: 3rem;
  }
  .a-comment p {
    font-size: 25px;
    font-weight: 700;
  }
  .asection p {
    font-size: 16px !important;
  }
}
@media only screen and (max-width: 480px) {
  .aboutvideosection {
    margin: 2rem 1rem;
    height: 50%;
  }
  .aboutimgsection {
    margin: 2rem 1rem;
  }
  .a-comment {
    margin: 2rem 6rem;
    height: 2rem;
  }
  .a-comment p {
    font-size: 20px;
    font-weight: 600;
  }
  .asection img {
    width: 50%;
  }
  .asection p {
    font-size: 10px !important;
  }
}
