.cart-page .container {
  width: 100%;
  /* border: 2px solid red; */
  /* border-style: none; */
  box-shadow: none;
}

.cart-page .row {
  --bs-gutter-x: 0rem;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
}
.col-md-4 img {
  width: 10rem;
  height: 10rem;
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  /* background-color: rgba(0, 0, 255, 0.072) !important; */
  background-color: white !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}

.cart-page .card {
  padding: 5px !important;
  height: fit-content !important;
  margin-bottom: 5px !important;
}

.scroll {
  height: 100vh;
  overflow-y: scroll;
}
.cart-page .cart-remove-btn {
  display: flex;
  align-items: right;
  justify-content: center;
}
.cart-page .cart-summary {
  text-align: center;
  /* margin-top: -140px !important; */
}
.btn-outline-warning {
  color: white;
  background: #32409a;
  border-style: none;
  /* font-weight: bold; */
}
.btn-outline-warning:hover {
  background: #212b67;
  color: white;
}

label {
  display: block;
}
.braintree-dropin {
  margin-right: -2rem;
  margin-left: 2rem;
}
