.product-details {
  padding: 2rem 20rem;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

.product-details-info h6 {
  margin-bottom: 20px !important;
  font-weight: bold;
}

.product-details .card-img-top {
  width: 300px !important;
  margin-left: 10px;
}

.similar-products .card {
  /* background-color: rgba(128, 128, 128, 0.097); */
  width: 18rem;
  border: none;
}
.similar-products {
  padding: 0 20rem;
  /* background: red; */
}
.similar-products .card-body {
  border: 1px solid rgba(128, 128, 128, 0.097);
}

.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
}

.similar-products .card-price {
  color: green;
  font-weight: bold;
}
.similar-products button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.similar-products .card-text {
  color: rgb(90, 89, 89);
}

.similar-products .card img:hover {
  transform: scale(1.1);
}

.similar-products .card-img-top {
  height: 300px !important;
}

@media only screen and (max-width: 1024px) {
  .product-details {
    padding: 2rem 10rem;
  }

  .product-details-info h6 {
    font-size: 20px;
  }

  .product-details .card-img-top {
    height: 20rem !important;
  }

  .similar-products {
    padding: 0 10rem;
  }
}
@media only screen and (max-width: 768px) {
  .product-details {
    padding: 2rem 5rem;
  }

  .product-details-info h6 {
    font-size: 18px;
  }

  .product-details .card-img-top {
    width: 220px !important;
    height: 15rem !important;
  }
  .p-details-img {
    padding-right: 0;
    display: flex;
    align-items: center;
  }
  .product-details-info {
    padding-left: 0;
  }

  .similar-products {
    padding: 0 5rem;
  }
  .similar-products .card-img-top {
    height: 15rem !important;
  }
}
