.best-products {
  height: 4rem;
  background: #32409a;
  color: #fff;
  /* margin: 0 6.5rem; */
  margin: 3rem 7rem;
  font-weight: 600;
  font-size: 20px;
  display: flex;
  padding: 1rem;
  align-items: center;
  /* margin-bottom: 3rem; */
}
.justify-content-between {
  /* margin: 0 6.2rem 2rem 6.2rem; */
  margin: 3rem 7rem;
}

.h-card {
  height: 18rem;
}

.free-shipping {
  background: #32409a;
  width: 59.6%;
  display: flex;
  text-align: center;
  justify-content: space-evenly;
  padding-top: 2rem;
  margin: 3rem 20rem;
}

.free-shipping img {
  margin-bottom: 1rem;
}

.phr-services {
  margin: 3rem 7rem;
  display: flex;
  object-fit: cover;
}

.s-img {
  width: 50%;
}

.s-content {
  /* background: hotpink; */
  width: 50%;
  /* height: 50%; */
  padding: 2rem 6rem;
}

.s-content h2 {
  font-weight: 600;
}

.s-content p {
  line-height: 2rem;
  text-align: justify;
}

.s-content ul li {
  font-weight: 500;
  line-height: 2.5rem;
  color: #32409a;
}

.s-content button {
  color: white;
  background: #32409a;
  height: 3.2rem;
  width: 12rem;
  margin-top: 2rem;
  font-size: 15px;
  border-style: none;
}
.prescription {
  width: 85vw;
  /* margin: 0 6.3rem 3rem 6.3rem; */
  margin: 3rem 7rem;
  /* background: pink; */
  height: 35rem;
  position: relative;
  overflow: hidden;
  font-family: "poppins";
}
.pres-card-body {
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  position: absolute;
  background: rgba(50, 64, 154, 0.8);
  /* z-index: 0.5; */
  /* backdrop-filter: blur(5px); */
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.prescription-title {
  text-transform: capitalize;
  font-size: 48px;
  font-family: "Oregano";
  line-height: 61px;
  /* font-weight: 400; */
}
.prescription-sub-title {
  text-transform: uppercase;
  font-size: 32px;
  font-weight: 500;
}
.prescription-btn {
  background: #fff;
  color: #32409a;
  padding: 1rem 2rem;
  border-radius: 2rem;
  border-style: none;
  font-size: 20px;
}
.phr-blog {
  display: flex;
  /* justify-content: space-between; */
  /* background: red; */
  /* margin: 0 6.3rem 3rem; */
  margin: 3rem 6rem;
  /* padding: 1rem; */
}
.p-blogs {
  padding: 0 1rem;
}
.phr-blog .card {
  /* background: yellow; */
  /* height: 100%; */
  border: 1.5px solid rgba(0, 0, 0, 0.1);
  /* height: 28rem; */
  /* width: 27rem; */
  /* padding: 1rem; */
}
.phr-blog .card img {
  border-radius: 0;
  object-fit: fill;
  height: 19rem;
}
.phr-blog .card .card-title {
  padding: 2rem 0;
}
.comment {
  background: #32409a;
  /* width: 30%; */
  display: flex;
  text-align: center;
  justify-content: space-evenly;
  padding-top: 2rem;
  margin: 3rem 35rem;
}

.comment-content {
  margin: 4rem 3rem 0rem 3rem;
}
.comment-content .card-title {
  display: flex;
  flex-direction: row;
  /* margin-right: 2rem; */
  /* border: 2px solid red; */
  /* justify-content: left; */
  margin-bottom: 0;
}

.comment-card-bg {
  /* background:linear-gradient(135deg, #078ce3 0%,#32409a 100%); */
  background: #32409a;
  background-image: url("../assets/Vector1.png");
  cursor: pointer;
  background-position: 70px 145px;
  background-repeat: no-repeat;
  color: #fff;
}

.comment-card-bg .card-body {
  background-image: url("../assets/Vector1.png");
  background-repeat: no-repeat;
  background-position: 70px 80px;
  height: 350px;
}

.comment-content .card-text {
  margin-top: 2rem;
  /* text-align: center; */
  text-align: justify;
}

@media only screen and (max-width: 1200px) {
  .best-products,
  .prescription {
    width: 81.3%;
  }
  .h-card {
    width: 8rem !important;
    height: 11rem;
  }
  h5 {
    font-size: 0.9rem !important;
  }
  .s-content {
    padding: 0 6rem;
  }
  .s-content h2 {
    font-size: 30px;
  }

  .s-content p {
    line-height: 1.5rem;
  }

  .s-content ul li {
    line-height: 1.5rem;
  }

  .s-content button {
    margin-top: 1.5rem;
  }
  .free-shipping {
    margin: 3rem 18.8rem;
    width: 50% !important;
  }
  .phr-blog .card img {
    height: 14rem;
  }
  .phr-blog .card .card-title {
    padding: 1rem;
  }
  .comment {
    margin: 3rem 27rem;
    width: 30%;
  }
}
