.contact-us {
  /* height: 100vh; */
  margin: 3rem 7rem;
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
}

.contact-map {
  /* width: 100%; */
  height: 35rem;
}

.contact-map iframe {
  width: 100%;
  height: 100%;
}

.contact-form {
  padding: 2rem 10rem;
}

.contact-form h1 {
  /* color: #000; */
  font: 500 1.8rem sans-serif;
  /* padding: 0.2rem; */

  text-align: center;
}

.contact-form form {
  padding: 0.2rem 0;
  /* background: red; */
  /* display: flex; */
}
.contact-form .input {
  display: flex;
  /* border: 2px solid green; */
  justify-content: space-between;
  margin: 2rem 0;
}

.contact-form .input .inputfeilds {
  width: 48%;
  /* display: flex; */
  /* flex-direction: row; */
  padding: 1rem;
  border: 0.08rem solid #cccccc;
  border-radius: 2rem;
  background: unset;
  /* color: #000; */
  /* font-size: 1rem; */
}
.contact-form textarea {
  width: 100%;
  padding: 1rem;
  border: 0.08rem solid #cccccc;
  border-radius: 2rem;
  background: unset;
  /* color: #000; */
  /* font-size: 1rem; */
  height: 20rem;
  resize: none;
}

.contact-form button {
  background: #7e7e7e;
  padding: 1rem 2rem;
  color: #fff;
  border: none;
  font-weight: 600;
  border-radius: 2rem;
  cursor: pointer;
  margin-top: 1.5rem;
}

.contact-form button:hover {
  background: #32409a;
}

@media only screen and (max-width: 1024px) {
  .contact-us {
    margin: 3rem 2rem;
  }
  .contact-form h1 {
    margin: 0;
  }
  .contact-form .input .inputfeilds {
    padding: 0.5rem;
  }
  .contact-form textarea {
    height: 15rem;
  }
  .contact-form button {
    padding: 0.7rem 1.5rem;
  }
  .contact-form button {
    margin-top: 0.5rem;
  }
}
@media only screen and (max-width: 768px) {
  .contact-us {
    margin: 3rem 2rem;
  }
  .contact-map {
    height: 20rem;
  }
  .contact-form textarea {
    height: 12rem;
  }
  .contact-form button {
    padding: 0.5rem 1rem;
  }
}

@media only screen and (max-width: 480px) {
  .contact-us {
    margin: 2rem 1rem;
  }
  .contact-map {
    height: 15rem;
  }
  .contact-form {
    padding: 1rem;
  }
}
