.swiper {
  width: 86%;
  height: 90%;
  /* margin-top: 3rem; */
  margin: 3rem 7rem;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* height: 600px; */
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide .b-img {
  display: block;
  width: 100%;
  /* height: 600px; */
  height: 38vw;
  object-fit: cover;
  cursor: pointer;
  /* height: fit-content; */
}
.swiper-slide .c-img {
  display: block;
  width: 100%;
  /* height: 100%; */
  object-fit: cover;
  cursor: pointer;
}
.swiper-pagination-bullet-active {
  background: #32409a;
}
.c-s-first {
  display: block;
}
.c-s-second {
  display: none;
}

@media only screen and (max-width: 1200px) {
  .swiper {
    width: 81.3%;
  }
}
@media only screen and (max-width: 1024px) {
  .swiper {
    width: 94.3%;
    margin: 3rem 2rem;
  }
  .cardsliderswiper {
    height: 182px !important;
  }
  .comment-section {
    height: 300px !important;
  }
}

@media only screen and (max-width: 768px) {
  .swiper {
    margin: 3rem 1.5rem;
  }
  .cardsliderswiper {
    height: 137px !important;
  }
}

@media only screen and (max-width: 480px) {
  .swiper-slide .b-img {
    height: 55vw;
  }
  .swiper {
    margin: 2rem 1rem;
  }
  .cardsliderswiper {
    height: 80px !important;
  }
  .c-s-first {
    display: none;
  }
  .c-s-second {
    display: block;
  }
}
@media only screen and (max-width: 320px) {
  .swiper {
    margin: 1rem 0.5rem;
  }
  .cardsliderswiper {
    height: 55px !important;
  }
  .c-s-first {
    display: none;
  }
  .c-s-second {
    display: block;
  }
}
