.servicepage {
  /* background: #000; */
  margin: 3rem 7rem;
  /* padding: 0; */
}
.subservicepage {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 3rem 0rem;
}
.service1 {
  background: #edeef5;
  width: 26rem;
  padding: 3rem;
  /* text-align: justify; */
  /* margin: 0; */
  /* line-height: 30px; */
}
.service1:hover {
  background: #32409a;
  .service1logo {
    display: none;
  }
  .serviceheading {
    color: #f5f4f4;
  }
  display: flex;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  .servicedescription {
    color: #e9e6e6;
  }
  transition: 1.3s;
}

.service1 .serviceheading p {
  margin-top: 0.5rem;
  font-size: 20px;
  /* line-height: 30px; */
  /* color: #808080; */
  font-weight: 600;
}
.service1 .serviceheading h4 {
  margin-top: 0.5rem;
}

.service1 .servicedescription {
  text-align: justify;
  color: #808080;
}
@media only screen and (max-width: 1200px) {
  .service1 {
    width: 20rem;
    padding: 1rem;
  }
  .service1 .serviceheading h4 {
    font-size: medium;
  }
}
@media only screen and (max-width: 1024px) {
  .servicepage {
    margin: 3rem 2rem;
  }
  .service1 {
    /* width: 19rem; */
    width: 30%;
  }
  .service1 .serviceheading h4 {
    font-size: 16px;
  }
  .service1 .servicedescription {
    font-size: 14px;
  }
}
@media only screen and (max-width: 768px) {
  .servicepage {
    margin: 3rem 1.5rem;
  }
  .service1 {
    padding: 1rem;
  }
  .service1 .serviceheading h4 {
    font-size: 15px;
  }
  .service1 .servicedescription {
    font-size: 13px;
  }
}

@media only screen and (max-width: 480px) {
  .subservicepage {
    margin: 2rem 0rem;
  }
  .servicepage {
    margin: 2rem 1rem;
  }
  .service1 {
    width: 45%;
  }
  .third-service {
    display: none;
  }
  .service1 .serviceheading h4 {
    font-size: 15px;
  }
  .service1 .servicedescription {
    font-size: 13px;
  }
}
@media only screen and (max-width: 320px) {
  .subservicepage {
    margin: 0.5rem 0rem;
    display: flex;
    flex-direction: column;
  }
  .servicepage {
    margin: 1rem 0.5rem;
  }
  .service1 {
    width: 100%;
    margin: 0.2rem 0;
  }
  .third-service {
    display: block;
  }
}
